import React from "react";
import { Card, CardBody } from 'react-bootstrap'; import { useSelector } from "react-redux";
import { MediaBaseUrl } from "../../../../../actions/api";
import { RecommendeionSkeleton } from "./Skeleton";
import EmptyWithIcon from "../../CommonComponents/Empty/EmptyWithIcon";
import { useNavigate } from "react-router-dom";

const Recommendation = ({ proUser, status = false }) => {
    const { recommendation, recommendationLoader } = useSelector((state) => state.analyticsOverView);
    const navigate = useNavigate();
    return (
        <>
            <Card className="sw-card-custom h-100 position-relative" style={{ display: status && proUser == 0 ? 'none' : 'block' }}>
                <div className="card-header min-h-auto py-3">
                    <div className="card-title mb-0 d-flex h-100 w-100">
                        <h5 className="card-label mb-0 font-weight-bolder d-flex align-items-center justify-content-between w-100">
                            {/* <span>AI Recommendation</span> */}
                            <h3 className="h5 mb-0">AI Recommendation</h3>
                            <span style={{ verticalAlign: "middle", fontStyle: "italic", fontSize: '11px', color: "#999", fontWeight: "400" }}>Powered By
                                <img src={`${MediaBaseUrl}media/icons/svg-icons/true-sense.svg`} alt="TruSense" height={16} width={16} />
                                <span style={{ verticalAlign: "middle", fontSize: '13px', fontStyle: "normal", color: "var(--bs-gray-900)" }}> TruSense</span>
                            </span>
                        </h5>
                    </div>
                </div>
                {/* {!proUser == 0 ? <BlurUnlock text={'Identify Recommended Posts for Data-Driven Decisions'} btn={IS_TAGEMBED ? 'Talk to Sales' : 'Upgrade Now'} actionHandle={IS_TAGEMBED? () =>  setShowSalesPop(true) : () => dispatch(togglePlanSection(true))} /> : null} */}
                <CardBody className="px-2 py-2">

                    {proUser == 0 ? <div className="d-flex flex-column align-items-center h-100 justify-content-center dsb-empty">
                        <EmptyWithIcon title="Know How Your Audience Feels!" desc={'Analyze your content and understand audience sentiment in real-time.'} icon={''} button={{ action: () => navigate(`/price`), text: 'Upgrade Now!' }} isFullScreen={false} />
                    </div> :
                        recommendationLoader ? <RecommendeionSkeleton /> :
                            recommendation?.map((item, index) => {
                                return (
                                    <div className="card-body px-2 py-2">
                                        <div className="px-5 py-5 border rounded  sw-hover">
                                            <div className="d-flex align-items-center">
                                                <div className="d-flex align-items-start me-2 flex-grow-1">
                                                    <div className="border rounded p-2 me-4">
                                                        <span
                                                            className={`bulits ${index == 0 ? 'bg-success' : index == 1 ? 'bg-warning' : 'bg-danger'}  d-block rounded-circle`}
                                                            style={{ width: "12px", height: "12px" }}
                                                        ></span>
                                                    </div>
                                                    <div>
                                                        <p className="text-dark mb-0 fw-bold text-truncate author_name h6"> {item.name}</p>
                                                        <p className="text-dark mb-0">{item.details}</p>
                                                    </div>
                                                </div>
                                                <p className="fw-bold text-dark h4 mb-0">{item.count}</p>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                    }

                </CardBody>
            </Card>
        </>
    );
};

export default React.memo(Recommendation);