import { Component } from "react";
import { connect } from "react-redux";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { Card, OverlayTrigger, Tooltip } from 'react-bootstrap';
import AreaChart from "../../CommonComponents/AreaChart";

const loaderData = {
    "series": [
        { "name": "Page views", "tool_tip": "Total no. of times your Shopon.bio page was viewed.", "data": [ ]},
    ],
    "labels": [""]
}
class PageView extends Component{

    state={pageData:null}

    componentWillMount(){
        const {loader, pageData} = this.props
        if(!loader){
            this.setState({pageData: pageData})
        }
    }

    componentWillReceiveProps(nextProps){
        if(this.props.loader != nextProps.loader){
            this.setState({pageData: nextProps.pageData})
        }
    }


    render(){
        const { pageData } = this.state
        const { loader } = this.props
        const series  = pageData?.series[0] ? pageData.series[0] : []
        return <Card className="card-stretch gutter-b">
                    <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
                        <div className="d-flex align-items-center">
                            <span className="h5 mb-0">{loader ? loaderData?.series[0].name : series.name}</span>
                            <div className="ms-2">
                                <OverlayTrigger placement='top' overlay={<Tooltip>{loader ? loaderData.series[0].tool_tip : series.tool_tip}</Tooltip>} >
                                    <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                                </OverlayTrigger>
                            </div>
                        </div>
                        <span className="d-flex align-items-center text-muted">{loader ? loaderData?.series[0].name : series.name} <span className="display-4 fw-bold ms-2 ">{loader ?  0 : series.total}</span></span>
                    </div>
                    <Card.Body>
                        <div className="chartArea"> 
                            <AreaChart chartData={loader ? loaderData : pageData} shopOn={loader ? false :true} serialNum={0}/>
                        </div>
                    </Card.Body>
                </Card>
    }
}

const mapStateToProps=(state)=>{
    return {
        pageData: state.analyticsShopOn?.shopOnOverview_data,
        loader: state.analyticsShopOn?.shopOnOverview_loader
    }
}

export default connect(mapStateToProps)(PageView)